footer{
    background-color: hsl(233, 26%, 24%);
    padding: 2rem 1rem;
    display: grid;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 1rem;
}

.section1{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.socialMediaIcons{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.socialMediaIcons img:hover, .socialMediaIcons img:focus{
    opacity: .7;
    cursor: pointer;
}
ul{
    display: flex;
    flex-direction: column;
    gap: .7rem;
}
ul li{
    color: #bebebe;
    letter-spacing: .8px;
    font-size: 18px;
    text-transform: capitalize;
}
ul li:hover, ul li:focus{
    color: hsl(136, 65%, 51%);
    cursor: pointer;
}

.lastSec{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}
.copyRight{
    color: hsl(233, 8%, 62%);
    font-size: 1.1rem;
}


@media(min-width:800px){
    footer{
        grid-template-columns: repeat(3,1fr);
        padding: 2rem 6rem;
        margin: 0;
    }
    .section1{
        gap: 2rem;
    }
    .lastSec{
        align-items: flex-end;
    }
    ul{
        gap: 1rem;
    }
    .uls{
        display: flex;
        text-align: left;
        align-items: center;
        gap: 2rem;
    }
}