main{
    display: grid;
    /* grid-template-columns: repeat(2,1fr); */
    background-color: hsl(220, 16%, 96%); 
    background-color: hsl(0, 12%, 97%); 
    padding-bottom: 1.7rem;

}
.imageSide{
    background-image: url(../images/bg-intro-mobile.svg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 79vh;
    overflow: hidden;
}
.imageSide img{
    width: 100%;
    height: 100%;
    margin-top: -6rem;
}

.textSide{
    text-align: center;
    padding: 0 1.2rem;
    /* margin-top: -1.5rem; */
    margin-bottom: 3rem;
}
.textSide h1{
    color: hsl(233, 26%, 24%);
}
.requestBtn{
    padding: .7rem 1rem;
    font-size: 16px;
    border-radius: 1.5rem;
    letter-spacing: .7px;
    font-weight: 700;
    cursor: pointer;
    border: none;
    background: linear-gradient(to right,hsl(136, 65%, 51%),hsl(192, 70%, 51%));
    color: #Fff;
}
.requestBtn:hover , .requestBtn:focus{
    opacity: .7;
}


@media (min-width:650px){
    main{
        grid-template-columns: repeat(2,1fr);
        position: relative; 
        overflow: hidden;
        /* height: 80vh;      */

    }
    .imageSide{
        background-image: url(../images/bg-intro-desktop.svg);
        position: absolute;
        right: 0;
        top: 0;
        width: 70%;
        height: 80vh;
        margin-right: -4rem;
        margin-top: -3rem;
    }
    .imageSide img{
        /* margin: 0 auto; */
        /* margin-right: -2rem; */
        /* margin-top: 0; */
        margin-top: 1.9rem;
        width: 85%;
        margin-left:6rem;
        /* background-color: rgba(80, 2, 2, 0.747); */

        
    }
    .textSide{
        width: 80%;
        text-align: left;
        padding: 8rem 2rem;
    }
    .textSide h1{
        font-weight: 300;
        font-size: 2.5rem;
    }
}


@media(min-width:928px){

    .imageSide{
        width: 60%;
    }
    .imageSide img{
        width: 65%;
        margin-left: 9rem;
    }
    .textSide{
        padding: 10rem 4rem;
        width: 75%;
    }
    .textSide h1{
        font-size: 3rem;
    }
}
