.container{
    text-align: center;
    padding: 1rem 1.5rem;
    background-color: hsl(0, 9%, 94%);
    
}
.a h2{
    color: hsl(233, 26%, 24%);
    font-weight: 400;
}

.miniTitles{
    color: hsl(233, 26%, 24%);
    letter-spacing: .9px;
    font-weight: 700;
    font-size: 1.2rem;
}
.b{
    display: grid;
    gap: 1.3rem;
    padding: .6rem;
    margin-top: 1rem;
}
/* template component */
.template{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: aqua; */
}
.iconStyles{
    margin-bottom: 1.8rem;
}
@media(min-width:600px){
    .container{
        text-align: left;
        padding: 1rem 3.5rem;
    }
    .a{
        width: 55%;
    }
    .b{
        grid-template-columns: repeat(2,1fr);
        text-align: center;
    }

}
@media(min-width:800px){
    .b{
        grid-template-columns: repeat(4,1fr);
        text-align: left;
    }
  
    .template{
        justify-content: flex-start;
        align-items: flex-start;
    }
}
