header{
    padding: 1.5rem;
    display: grid;
    gap: 1rem;
}

.logoSec{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.close{
    display: none;
}
nav{
    position: absolute;
    background-color: #FFF;
    width: 80%;
    top: 16%;
    left: 8%;
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    display: none;
    z-index: 10;
}
li{
    list-style-type: none;
}
nav ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
}
.navLinks{
    list-style-type: none;
    color: hsl(233, 26%, 24%);
    cursor: pointer;
    margin-left: -1.9rem;
}
nav .requestBtn{
    display: none;
}
header .requestBtn{
    display: none;
}
.hamburger, .close{
    cursor: pointer;
}
.hamburger:hover, .hamburger:focus, .close:hover, .close:focus{
    opacity: .5;
}


@media(min-width:800px){
    header{
        background-color: #fff;
        padding: 1rem 3rem;
        grid-template-columns: repeat(3,1fr);
        justify-content: space-between;
        gap: 3rem;
        position: sticky;
        top: 0%;
    }
    .hamburger, .close{
        display: none;
    }
    nav{
        margin-left: -5rem;
        position: static;
        background: none;
        flex-direction: row;
        padding: 0;
        display: flex;
    }
    nav ul{
        flex-direction: row;
    }
    .navLinks{
        margin: 0;
        color: hsl(233, 8%, 62%);

    }
    header .requestBtn{
        display: flex;
        align-self: center;
        justify-self: center;
    }
}