.articlesContainer{
    padding: 1rem 2rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
}
.article{
    display: grid;
    gap: 2rem;
}

.articlesContainer h2{
    color: hsl(233, 26%, 24%);
    letter-spacing: .9px;
    text-align: center;
    font-weight: 400;
    font-size: 1.7rem;
}
.ArticlesTemplate img{
    width: 100%;
    border-radius: .5rem .5rem 0 0;
}


.desc{
    padding: 1.5rem 2rem;
    background-color: #fff;
    border-radius: 1rem;
}
.desc h3{
    color: hsl(233, 26%, 24%);
    letter-spacing: .9px;
    font-weight: 400;
}



@media(min-width:600px){
    .article{
        grid-template-columns: repeat(2,1fr);
        
    }
    .articlesContainer{
        padding: 1rem 2rem;
    }

}

@media(min-width:800px){
    .articlesContainer{
        padding: 0 6rem;
    }
    .article{
        grid-template-columns: repeat(4,1fr);
    }
    .articlesContainer h2{
        text-align: left;
    }
    .ArticlesTemplate{
        display: flex;
        flex-direction: column;
        gap: .5rem;
    }
    .ArticlesTemplate img{
        border-radius: none;
        height: 50%;

    }
    .desc{
        padding: .5rem 1rem;
        /* height: 50%; */
    }
    .desc p{
        font-size: .9rem;
        margin-top: -.8rem;
    }
    small{
        margin-bottom: -1rem;
    };
}